@import "./modules/colors.scss", "./modules/mixins.scss";
@font-face {
    font-family: "dealerplate";
    src: url("../fonts/dealerplate\ california.ttf");
}

.services-container-desc {
    background-image: url("../images/bg-page.jpeg");
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-size: 100% 200%;
    margin-top: $header-size;
    padding: 30px;
}

.services-list-desc {
    // @include phone {
    //     flex-flow: column wrap;
    //     gap: 0px;
    // }
    @include flex-align(center, center);
    flex-flow: row wrap;
    gap: 50px;
}

li {
    @include flex-align(center, center);
    flex-flow: row nowrap;
    width: 100%;
    gap: 30px;

    .text-container {
        @include phone {
            height: fit-content;
            width: 300px;
            font-size: 0.8rem;
        }
        word-break: break-word;
        position: relative;
        margin: 0;
        top: 0;
        color: $font-color;
        font-size: 2vw;
        background: $body-color;
        padding: 20px;
        border-radius: 10px;
        font-family: "dealerplate";
        text-align: justify;
    }
}


/* Estilizando tabela */
#service-danger-table {
    @include flex-align(center, center, center);
    flex-flow: column wrap;
    text-align: center;
    width: 70%;
    margin: auto;

    tr {
        display: block;
        width: 100%;
    }
}

@each $danger, $color in $danger-colors {
    .#{$danger} {
        display: block;
        width: 100%;
        background-color: $color;
        padding: 10px;

        @if $danger == 'critic' {
            color: $body-color;
        }
    }
}
